          gate: "Gate 3",
        },
      ],
      flightInfo: {},
      today: new Date(),
    };
  },
  computed: {
    // flightDetails() {
    //   console.log("From computed");
    //   this.flightInfo = this.$store.getters.getFlightData;
    //   // return this.$store.getters.getFlightData
    // },
    // getSchedue(){
    //     return moment((this.$store.getters.getFlightData)['scheduleTime']).format("HH:mm");
    // }
  },
  mounted() {
    console.log("mounted TranckFlight");
    console.log(this.getFlightInfo());
  },
  methods: {
    getFlightInfo() {
      this.flightInfo = this.$store.getters.getFlightData;
      return this.$store.getters.getFlightData;
    },
    getTime(timestamp) {
      return moment(timestamp).format("HH:mm");
    },

    getColor(status) {
      if (status == "Delayed") return "red";
      else if (status == "Gate Closed") return "red";
      else if (status == "Final Call") return "red";
      else if (status == "Departed") return "red";
      else if (status == "Check-in Open") return "blue";
      else if (status == "Boarding") return "blue";
      else if (status == "Security Check") return "blue";
      else if (status == "Arrived") return "success";
      else if (status == "On Time") return "success";
      else return "success";
    },
    refresh() {
      axios
        .post(process.env.VUE_APP_API_URL + `flightsroute/getdataforlist`, {
          list: [`${this.flightInfo["id"]}`],
        })
        .then((flightdetails) => {
          this.today = new Date();
          console.log(new Date().toLocaleDateString('en-us', { weekday:"long", year:"numeric", month:"short", day:"numeric",hour:"2-digit" ,minute:"2-digit",second:"2-digit"}) )
           console.log(flightdetails);
          let element = flightdetails.data.data[0];
          console.log("this.refresh", element);
          let scheduled = new Date(element.scheduledArrivalDate);
          let estimated = new Date(element.estimatedArrivalDate);
          element["img"] =
            "http://development.wisefly.in/flightlogo/getimage/" +
            element.airlineCode +
            ".png";

          element["scheduled"] =
            scheduled.getHours() + ":" + scheduled.getMinutes();

          element["estimated"] =
            estimated.getHours() + ":" + estimated.getMinutes();
          this.flightInfo = element;
        });
    },
  },
};
</script>
